import React from 'react';
import {
  IMessage,
  InputToolbar,
  InputToolbarProps,
} from 'react-native-gifted-chat';
import {Platform} from 'react-native';
import {ChatComposer} from './composer';
import {ChatSendButton} from './send';
import {useTheme} from 'react-native-paper';
import {useAppSelector} from '../../../store/hooks';
// import { useAppTheme } from '../../../../../utils/hooks/useAppTheme'
// import { useAppSelector } from '../../../../../store/hooks'

export function ChatInputToolbar({
  inputProps,
  chatId,
}: {
  inputProps: InputToolbarProps<IMessage>;
  chatId: string;
}) {
  const draftMessage = useAppSelector(
    state => state.chat.draftMessage[chatId],
  );
  const keyheight = useAppSelector(state => state.ui.keyboardHeight);
  const theme = useTheme();

  return (
    <InputToolbar
      {...inputProps}
      containerStyle={{
        borderTopWidth: 0,
        backgroundColor: theme.colors.surface,
        borderColor: 'transparent',
        minHeight: 120,
        paddingTop: 20,
        height: 'auto',
        shadowColor: 'transparent',
        bottom: keyheight,
      }}
      primaryStyle={{
        borderColor: 'transparent',
        shadowColor: 'transparent',
      }}
      renderComposer={composerProps => (
        <ChatComposer
          chatId={chatId}
          composerProps={{...composerProps}}
        />
      )}
      renderSend={sendProps =>
        draftMessage ? (
          <ChatSendButton
            sendProps={{...sendProps}}
            chatId={chatId}
          />
        ) : null
      }
    />
  );
}
