import React from 'react';
import {Platform} from 'react-native';
import {Composer, ComposerProps} from 'react-native-gifted-chat';
import {IconButton, useTheme} from 'react-native-paper';
import {setDraftMessage} from '../../../store/slices/chat';
import {useAppDispatch, useAppSelector} from '../../../store/hooks';
import {useFileSelectOptions} from '../../../hooks/useFileSelectOptions';
import {AttachmentDrawer} from './attachmentdrawer';
export function ChatComposer({
  composerProps,
  chatId,
}: {
  composerProps: ComposerProps;
  chatId: string;
}) {
  const dispatch = useAppDispatch();

  const theme = useTheme();
  const draftMessage =
    useAppSelector(state => state.chat.draftMessage[chatId]) || '';

  const fileSelectOptions = useFileSelectOptions(chatId, true);

  return (
    <>
      <IconButton
        {...composerProps}
        icon={'plus'}
        iconColor={theme.colors.onSurface}
        size={28}
        style={{
          padding: 0,
          position: 'absolute',
          zIndex: 2,
          top: Platform.OS === 'ios' ? -23 : 0,
        }}
        onPress={fileSelectOptions.showDrawer}
      />
      <Composer
        {...composerProps}
        placeholder={'Type your message here'}
        onTextChanged={text =>
          dispatch(setDraftMessage({id: chatId, content: text}))
        }
        placeholderTextColor={theme.colors.onSurfaceVariant}
        textInputAutoFocus
        text={draftMessage}
        textInputStyle={{
          ...theme.fonts.default,
          color: theme.colors.onSurface,
          padding: 20,
          paddingTop: 0,
          paddingLeft: 50,
          paddingRight: 50,
          paddingBottom: 40,
          // marginBottom: 20,
          backgroundColor: theme.colors.surface,
          borderRadius: 30,
          width: '100%',
        }}
        multiline
        textInputProps={{
          ...composerProps.textInputProps,
        }}
      />
      <AttachmentDrawer {...fileSelectOptions} />
    </>
  );
}
