import React, {memo, useRef} from 'react';
import {Platform, View, useWindowDimensions} from 'react-native';
import {
  Banner,
  Divider,
  IconButton,
  useTheme,
} from 'react-native-paper';
import {useQuery} from 'react-query';
import {EditorLayer, EditorProject} from '../../types';
import EncoderProject from '../encoder/encoderproject';
import EditorForm from './editorform';
import EditorVideoComponent from './editorvideo';
import {ScrollView} from 'react-native-gesture-handler';
import {getFolderContent, getById, writeItem} from '../../api';
import Chat from '../contacts/chat';
import {EditorPlayControls} from './editorplaycontrols';
import {ProjectParams} from '../../types/nav';
import {useNavigation} from '@react-navigation/native';
import {useAppSelector} from '../../store/hooks';
function EditorBanner({
  projectId,
  height,
  routeParams,
}: {
  projectId: string;
  height: number;
  routeParams: ProjectParams;
}) {
  const theme = useTheme();
  const mediaData = useQuery(
    ['projectdata', projectId],
    async () => await getById(projectId),
    {
      enabled: projectId ? true : false,
    },
  );
  const showConfig = useAppSelector(state => state.editor.showConfig);
  const video2 = useRef<JSX.IntrinsicElements['video'] | undefined>();
  const mediaRes: EditorProject = mediaData.data;

  const layersQuery = useQuery(
    ['projectLayers', projectId],
    async () => {
      const data = await getFolderContent(projectId);
      const m = await Promise.all(
        data.map(async i => {
          return {
            ...i,
            media: await getFolderContent(i?.id),
          };
        }),
      );
      return m;
    },
  );
  const {width} = useWindowDimensions();
  const layers: EditorLayer[] | undefined = layersQuery?.data;
  const isMobile = width < 700 || Platform.OS !== 'web';
  const hasSelection =
    routeParams.encoder !== undefined ||
    routeParams.chatId !== undefined;
  if (isMobile) {
    return (
      <View
        style={{margin: 0, padding: 0, display: 'flex'}}
        className="video-banner">
        {/* <Divider /> */}
        <Banner
          visible={projectId ? true : false}
          theme={theme}
          style={{
            backgroundColor: 'black',
            padding: 0,
            margin: 0,
            // height: height/3,
            width,
            marginHorizontal: 0,
            paddingHorizontal: 0,
            overflow: 'hidden',
            display: 'flex',
            flexDirection: 'row',
          }}
          contentStyle={{
            width,
            flex: 1,
            height: '100%',
            padding: 0,
            margin: 0,
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'stretch',
            overflow: 'hidden',
            marginHorizontal: 0,
            paddingHorizontal: 0,
          }}>
          <View
            style={{
              width,
              height: isMobile ? height / 2.5 : height / 2,
              borderLeftWidth: isMobile ? 0 : 1,
              borderRightWidth: isMobile ? 0 : 1,
              margin: 0,
              padding: 0,
              borderLeftColor: 'rgb(73, 69, 79)',
              borderRightColor: 'rgb(73, 69, 79)',
              display: 'flex',
              overflow: 'hidden',
            }}
            key={2}>
            {showConfig && (
              <ScrollView
                style={{
                  width,
                  height: height / 2,
                  display: 'flex',
                  marginRight: 5,
                }}
                key={1}
                showsHorizontalScrollIndicator={false}
                showsVerticalScrollIndicator={false}>
                <EditorForm
                  projectId={projectId}
                  mediaRes={mediaRes}
                  video2={video2}
                  layers={layers}
                  projectQuery={mediaData}
                  writeItem={writeItem}
                />
              </ScrollView>
            )}
            {hasSelection && !showConfig ? (
              <View
                style={{
                  width,
                  height: height / 2,
                  display: 'flex',
                  paddingBottom: 100,
                }}
                key={3}>
                {routeParams?.encoder && (
                  <EncoderProject params={routeParams} />
                )}
                {routeParams?.chatId && (
                  <Chat isEmbed={true} params={routeParams} />
                )}
              </View>
            ) : (
              <>
                {!showConfig && (
                  <>
                    <EditorVideoComponent
                      video2={video2}
                      projectId={projectId}
                      mediaRes={mediaRes}
                      layers={layers}
                    />
                    <EditorPlayControls
                      mediaRes={mediaRes}
                      layers={layers}
                    />
                  </>
                )}
              </>
            )}
          </View>
        </Banner>
      </View>
    );
  }
  return (
    <View
      style={{margin: 0, display: 'flex'}}
      className="video-banner">
      <Divider />
      <Banner
        visible={projectId ? true : false}
        theme={theme}
        style={{
          backgroundColor: 'black',
          padding: 0,
          margin: 0,
          height: '100%',
          display: 'flex',
          flexDirection: 'row',
          overflow: 'visible',
        }}
        contentStyle={{
          width,
          height: '100%',
          maxWidth: 'auto',
          padding: 0,
          margin: 0,
          display: 'flex',
          flexDirection: 'row',
          alignItems: 'stretch',
          overflow: 'visible',
        }}>
        <View
          style={{
            display: 'flex',
            flexDirection: 'row',
            width,
            height: '100%',
          }}>
          <ScrollView
            style={{
              width: '24%',
              height: height / 2,
              display: 'flex',
              marginRight: 5,
            }}
            key={1}
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}>
            <EditorForm
              projectId={projectId}
              mediaRes={mediaRes}
              video2={video2}
              layers={layers}
              projectQuery={mediaData}
              writeItem={writeItem}
            />
          </ScrollView>
          <View
            style={{
              width: hasSelection ? '45%' : '74%',
              height: height / 2,
              borderLeftWidth: 1,
              borderRightWidth: 1,
              borderLeftColor: 'rgb(73, 69, 79)',
              borderRightColor: 'rgb(73, 69, 79)',
              position: 'relative',
              display: 'flex',
            }}
            key={2}>
            <EditorVideoComponent
              video2={video2}
              projectId={projectId}
              mediaRes={mediaRes}
              layers={layers}
            />
            <EditorPlayControls
              width={'100%'}
              mediaRes={mediaRes}
              layers={layers}
            />
          </View>
          {hasSelection && (
            <View
              style={{
                width: '30%',
                height: height / 2,
                display: 'flex',
              }}
              key={3}>
              {routeParams?.encoder && (
                <EncoderProject params={routeParams} isEmbed={true} />
              )}
              {routeParams?.chatId && (
                <Chat
                  isEmbed={true}
                  params={routeParams}
                  height={height / 2}
                />
              )}
            </View>
          )}
        </View>
      </Banner>
      <Divider />
    </View>
  );
}

export default memo(EditorBanner);
